.empty {
  @include empty();
  width: adaptive-size(40, 20);
  height: adaptive-size(40, 20);
}

.cell {
  width: adaptive-size(40, 20);
  height: adaptive-size(40, 20);
  @include cell();

  cursor: pointer;
  position: relative;
  transition: 0.4s;

  @media (any-hover: hover) {
    &:hover {
      @include cell-hover();
    }
  }
}

.hint {
  width: adaptive-size(40, 20);
  height: adaptive-size(40, 20);
  @include hint();
  @include font-hint();
  text-align: center;
  vertical-align: center;

  &--off {
    @include font-hint-off();
  }
}

.fill {
  @include fill;
}

.crossword {
  border-collapse: collapse;
  @include crossword();
}

.cross::before,
.cross::after {
  content: "";
  position: absolute;
  @include cross();
}

.cross::before {
  height: 2px;
  width: 50%;
  top: 50%;
  left: 25%;
  transform: translateY(-50%) rotate(45deg);
}

.cross::after {
  width: 2px;
  height: 50%;
  top: 25%;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
}

.border-left {
  @include border-left();
}
.border-top {
  @include border-top();
}
.border-right {
  @include border-right();
}
.border-bottom {
  @include border-bottom();
}
